import React, { useState } from 'react';
import { Route, Switch } from 'react-router-dom';

import { PUBLIC_URL } from './routes/routes';

import HomePage from './pages/homepage/homepage.component';
import FlightImagesPage from './pages/flight-images/flight-images.component';
import FlightDataPage from './pages/flight-data/flight-data.component';

import Header from './components/header/header.component';
import NavBar from './components/nav-bar/nav-bar.component';

import './App.css';

import { Layout } from 'antd';
const { Sider, Content } = Layout;

const App = () => {
  const [collapsed, setCollapsed] = useState(true);

  const toggleCollapsed = () => setCollapsed(!collapsed);

  return (
    <Layout style={{ minHeight: '100vh' }}>
      <Sider
        collapsible
        collapsed={collapsed}
        onCollapse={toggleCollapsed}
        theme='light'
        width={300}
      >
        <h1 className='logo'>HI04</h1>
        <NavBar />
      </Sider>
      <Layout>
        <Header />
        <Content className='content-layout'>
          <Switch>
            <Route exact path={`${PUBLIC_URL}`} component={HomePage} />
            <Route
              exact
              path={`${PUBLIC_URL}/flight-data`}
              component={FlightDataPage}
            />
            <Route
              exact
              path={`${PUBLIC_URL}/flights/:flightId/images`}
              component={FlightImagesPage}
            />
          </Switch>
        </Content>
      </Layout>
    </Layout>
  );
};

export default App;
